html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-decoration: none;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background-color: fff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
* {
  font-family: "Comic Neue", cursive;
}
.Wrapper {
  display: flex;
  width: 80%;
  justify-content: center;
}
.Accordion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  min-width: 400px;
  height: auto;
  background: #fff;
  padding-left: 20%;
  /* padding-left: 30vh;
  padding-right: 30vh; */
  padding-bottom: 10vh;
}

.AccordionWrap {
  background-color: #f7f6f5;
  display: flex;
  color: #e10d0d;
  justify-content: space-between;
  align-items: center;
  font-family: "Comic Neue", cursive;
  font-weight: lighter;
  font-size: 25px;
  width: 100%;
  padding: 15px;
  margin: 10px;
  cursor: pointer;
  border-radius: 60px;
}

.AccordionWrap:hover {
  background-color: #fff;
}

.AccordionDropdown {
  padding: 15px;
  display: flex;
  background-color: #fff;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  border-radius: 30px;
}

.AccordionDropdown button {
  background-color: #f7f6f5;
  margin: 3px;
  border: none;
  padding: 10px;
  font-size: 20px;
  color: #e10d0d;
  font-weight: bold;
  cursor: pointer;
  border-radius: 30px;
}
.AccordionDropdown button:hover {
  background-color: #fff;
}
.AccordionDropdown:hover {
  background-color: #fff;
}

