body {
  background-color: #fff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

* {
  font-family: "Comic Neue", cursive;
}
.SelectedRecipeContainer {
  display: flex;
  justify-content: flex-start;
  margin: auto;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 80%;
}

.SelectedRecipe {
  display: flex;
  flex-direction: row;
}

.SelectedRecipeImage {
  padding-right: 5vh;
  padding-bottom: 5vh;
}
h1 {
  font-size: 20px;
  font-family: "Comic Neue", cursive;
  color: red;
}

.NavContainer {
  display: flex;
  background-color: #fff;
}

.NavLeft {
  display: flex;
  align-items: center;
  width: 33%;
  font-family: "Comic Neue", cursive;
  font-size: 20px;
  color: red;
  padding-left: 1.5em;
}

.bold {
  font-weight: bold;
  color: #e10d0d;
  font-size: 58px;
}

.thin {
  font-weight: lighter;
  color: #e10d0d;
  font-size: 28px;
}
.thinCenter {
  display: flex;
  font-family: "Comic Neue", cursive;
  font-weight: lighter;
  color: #e10d0d;
  font-size: 20px;
  justify-content: center;
  align-items: center;
  width: 33%;
}

.NavRight {
  font-family: "Comic Neue", cursive;
  font-size: 20px;
  color: #e10d0d;
  display: flex;
  justify-content: flex-end;
  width: 33%;
  padding-right: 1.5em;
}

ul {
  display: flex;
  flex-direction: row;
}

.NavLink {
  padding: 0.9em;
  color: #e10d0d;
}

.NavLink:hover {
  font-size: 25px;
  border-radius: 10px;
}

@media only screen and (max-width: 600px) {
  .thinCenter {
    display: none;
  }
  .NavRight {
    width: 70%;
  }
}

.ResultsRecipeContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: space-around;
  max-width: 80%;
  margin: 2em auto;
  padding: 0 20px;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  border-radius: 30px;
}
.ResultsRecipeTitle h1 {
  max-width: 60%;
  display: flex;
  text-align: center;
  align-items: center;
  font-size: 20px;
  justify-content: center;
}

.ResultsRecipeImage img:hover {
  opacity: 0.4;
}

.FooterContainer {
  display: flex;
  background-color: #f7f6f5;
  padding-top: 5vh;
  padding-bottom: 5vh;
}

.FooterLeft {
  display: flex;
  align-items: center;
  width: 30%;
  font-family: "Comic Neue", cursive;
  font-size: 20px;
  color: red;
  padding-left: 1.5em;
}

.thin {
  font-weight: lighter;
  color: #e10d0d;
  font-size: 28px;
}

.FooterRight {
  font-family: "Comic Neue", cursive;
  font-size: 20px;
  color: #e10d0d;
  display: flex;
  justify-content: flex-end;
  width: 70%;
  padding-right: 1.5em;
}

ul {
  display: flex;
  flex-direction: row;
}

.NavLink {
  padding: 0.9em;
  color: #e10d0d;
}

.NavLink:hover {
  font-size: 23px;
  border-radius: 10px;
}

.thin:hover {
  font-size: 25px;
  border-radius: 10px;
}

